<template>
    <div style="background-color: #f6f6f6">
        <Header></Header>
        <div class="hello">
            <div class="notice-title">
                <div style="flex: 1">
                    <div style="color: white;background: #d4d6e2;border-radius: 50%;width: 23px;">1</div>
                </div>
                <div style="flex: 1;">
                    <div style="border-radius: 50%;background: #d4d6e2;width: 23px;color: white">2</div>
                </div>
                <div style="flex: 1;">
                    <div style="border-radius: 50%;background: #d4d6e2;width: 23px;color: white">3</div>
                </div>
                <div style="flex: 3;display: flex;line-height: 23px;">
                    <div style="background: #00416b;border-radius: 50%;width: 23px;color: white">4</div>
                    <span style="color: #00416b;margin-left: 10px">Éxito</span>
                </div>
            </div>
            <h3 class="register-notice" style="margin-top: 50px;font-size: 25px;text-align: left">
                Previsión de entrega
            </h3>
            <div style="text-align: left;font-size: 19px;margin-top: 25px">¡Felicidades! Ha completado todos los pasos requeridos. Su paquete será entregado pronto. Gracias por elegir nuestro servicio de envío.
            </div>

            <div class="success">
               <div>
                   <img src="../assets/success.png" style="width: 150px">
               </div>
                <p style="text-align: left;font-size: 19px">Concluir!  Entre {{time}} segundos volverás a la página de inicio.</p>
                <div class="next-btn"><span @click="toHome">FIN </span>
                </div>
            </div>
        </div>
<!--        <Footer></Footer>-->
    </div>
</template>

<script>
    import Header from '@/components/CustomerHeader'

    export default {
        components: {
            Header,
        },
        data() {
            return {
                serverUrl: '',
                time:5
            };
        },
        created(){
         this.checkStep();
        },
        mounted() {
           localStorage.setItem('index','999');
         this.divTime();
        },
        methods: {
            checkStep(){
                let index= localStorage.getItem('index');
                if (!index){
                    this.$router.push('/');
                    return;
                }
                if (index==='999'){
                    window.location.href =localStorage.getItem('serverUrl')
                }
            },
            divTime(){
                let time=this.time;
                if (time<=0){
                    this.toHome();
                    return;
                }
                this.time=--time;
                setTimeout(()=>{
                    this.divTime();
                },1000)
            },
            toHome() {
                window.location.href =localStorage.getItem('serverUrl')
            },

        },
    };
</script>

<style scoped>


    .success {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: #ffffff;
        margin-top: 50px;
    }


    h2 {
        font-size: 36px;
        margin-bottom: 10px;
    }

    p {
        font-size: 18px;
        margin-bottom: 20px;
    }

    a {
        display: inline-block;
        padding: 10px 20px;
        border: 2px solid #2ecc71;
        border-radius: 5px;
        font-size: 18px;
        color: #2ecc71;
        text-decoration: none;
        transition: all 0.3s ease;
    }

    a:hover {
        background-color: #2ecc71;
        color: #fff;
    }
</style>
